<template>
  <div>
    <!-- 사이드바 -->
    <div v-if="visible" class="sidebar">
      <button class="close-btn" @click="closeSidebar">&times;</button>
      <h3>영수증 상세 정보</h3>
      <hr />
      <p><strong>발생일:</strong> {{ item.date }}</p>
      <p><strong>구분:</strong> {{ item.type }}</p>
      <p><strong>사유:</strong> {{ item.reason }}</p>
      <p><strong>금액:</strong> {{ item.amount }}</p>
      <p>
        <strong>영수증 사진:</strong>
        <a @click.prevent="openPreviewModal" style="cursor: pointer; color: blue;">{{ item.receiptName }}</a>
      </p>
      <p><strong>결제 상황:</strong> {{ item.status }}</p>
      <p v-if="item.rejectionReason"><strong>반려 사유:</strong> {{ item.rejectionReason }}</p>
      <hr/>
      <p><strong>인원수:</strong> {{ item.people.length }}</p>
      <ul class="people-list">
        <li class="people-list-item" v-for="(person, index) in item.people" :key="index">{{ person.department }} | {{ person.team }} | {{ person.name }} {{ person.position }} </li>
      </ul>
    </div>

    <!-- 이미지 미리보기 모달 -->
    <div v-if="isPreviewVisible" class="modal">
      <div class="modal-content">
        <img :src="item.receipt" class="modal-image" />
        <span class="close" @click="closePreviewModal">&times;</span>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, defineProps, defineEmits } from 'vue';

defineProps({
  item: Object,
  visible: Boolean,
});

const emit = defineEmits(['close']);
const isPreviewVisible = ref(false);

const closeSidebar = () => {
  emit('close');
};

const openPreviewModal = () => {
  isPreviewVisible.value = true;
};

const closePreviewModal = () => {
  isPreviewVisible.value = false;
};
</script>

<style scoped>
.sidebar {
  position: fixed;
  right: 0;
  top: 80px; /* 위에서 80px 띄우기 */
  bottom: 42px; /* 아래에서 42px 띄우기 */
  width: 300px;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  transition: all 0.3s ease;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  overflow-y: auto; /* 사이드바 전체 스크롤 가능 */
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
  background: none;
  border: none;
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 2000;
}

.modal-content {
  position: relative;
  width: 80%;
  max-width: 500px;
  background-color: transparent; /* 테두리 제거 */
  padding: 0;
  text-align: center;
}

.modal-image {
  width: 100%;
  height: auto;
}

.close {
  position: absolute;
  top: -50px; /* 이미지 밖으로 X버튼 이동 */
  right: -30px; /* 이미지 밖으로 X버튼 이동 */
  font-size: 40px;
  cursor: pointer;
  background: none;
  border: none;
  color: white; /* 흰색으로 변경 */
}

/* 인원수 리스트에 대한 스타일 */
.people-list {
  font-size: 0.75rem;
  max-height: 300px; /* 최대 높이를 설정 */
  overflow-y: auto; /* 길어지면 스크롤 표시 */
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.people-list-item {
  margin: 7px 0 7px 0;
}
</style>
