<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content" :class="{ 'content-expanded': sidebarVisible && !isMobile }">
      <h2 class="content-title">영수증 등록</h2>
      <p class="content-sub-title">영수증 등록 페이지</p>

      <div class="d-flex justify-content-end align-items-center">
        <button class="btn btn-primary search-button" @click="openModal">영수증 등록</button>
      </div>

      <!-- 테이블 보기 (큰 화면에서만 보임) -->
      <table class="table mt-3" v-if="!isMobile">
        <thead>
          <tr>
            <th>발생일</th>
            <th>구분</th>
            <th>인원수</th>
            <th>사유</th>
            <th>금액</th>
            <th>결제 상황</th>
          </tr>
        </thead>
        <tbody>
          <template v-for="item in paginatedData" :key="item.date">
            <tr @click="openSidebar(item)">
              <td>{{ item.date }}</td>
              <td>{{ item.type }}</td>
              <td @click.stop="togglePeopleList(item)">{{ item.people.length }}</td>
              <td>{{ item.reason }}</td>
              <td>{{ item.amount }}</td>
              <td :class="getStatusClass(item.status)">{{ item.status }}</td>
            </tr>
            <!-- 인원 목록을 조건부로 표시 -->
            <template v-if="showPeopleList(item)">
              <tr v-for="person in item.people" :key="person.name">
                <td colspan="6">{{ person.name }} ({{ person.department }} - {{ person.team }})</td>
              </tr>
            </template>
          </template>
        </tbody>
      </table>

      <!-- 카드 레이아웃 보기 (작은 화면에서만 보임) -->
      <div class="card-layout" v-if="isMobile">
        <div class="card" v-for="item in paginatedData" :key="item.date">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong>인원:</strong> <span @click="togglePeopleList(item)" style="cursor: pointer;">{{ item.people.length }}명</span></p>
            <div v-if="showPeopleList(item)">
              <div class="people-list">
                <div class="people-list-header">명단</div>
                <p v-for="person in item.people" :key="person.name" class="people-list-item">{{ person.name }} ({{ person.department }} - {{ person.team }})</p>
              </div>
            </div>
            <p class="card-text"><strong>구분:</strong> {{ item.type }}</p>
            <p class="card-text"><strong>사유:</strong> {{ item.reason }}</p>
            <p class="card-text"><strong>금액:</strong> {{ item.amount }}</p>
            <!-- 추가된 부분: 영수증 사진 이름 (클릭 시 미리보기) -->
            <p>
              <strong>영수증 사진:</strong>
              <a @click.prevent="openPreviewModal(item.receipt)" style="cursor: pointer; color: blue;">{{ item.receiptName }}</a>
            </p>
            <!-- 추가된 부분: 반려 사유 -->
            <p class="card-text">
              <strong>반려 사유: </strong> {{ item.rejectionReason || 'X' }}
            </p>
            <p class="card-text">
              <strong>결제 상황:</strong> <span :class="getStatusClass(item.status)">{{ item.status }}</span>
            </p>
          </div>
        </div>
      </div>

      <!-- 페이지네이션 버튼 -->
      <nav>
        <ul class="pagination justify-content-center" id="pagination">
          <li class="page-item" :class="{ active: page === currentPage }" v-for="page in totalPages" :key="page">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
          </li>
        </ul>
      </nav>
    </div>

    <!-- 모달 창 추가 -->
    <div class="modal fade" id="receiptModal" tabindex="-1" role="dialog" aria-labelledby="receiptModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="receiptModalLabel">영수증 등록</h5>
            <button type="button" class="close" aria-label="Close" @click="closeModal">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form @submit.prevent="submitReceipt">
              <div class="form-group">
                <label for="receiptImage">영수증 사진</label><br>
                <div class="mt-2">
                  <input type="file" class="form-control-file" id="receiptImage" @change="handleFileChange" />
                </div>
              </div><br>
              <div class="form-group">
                <label for="date">발생일</label>
                <input type="date" class="form-control" id="date" v-model="formData.date" />
              </div><br>
              <div class="form-group">
                <label for="type">구분</label>
                <select class="form-control" id="type" v-model="formData.type">
                  <option>식비</option>
                  <option>교통비</option>
                </select>
              </div><br>
              <div class="form-group">
                <label for="amount">금액</label>
                <div class="input-group">
                  <input type="text" class="form-control" id="amount" placeholder="내용을 입력해주세요" v-model="formData.amount" />
                  <div class="input-group-append">
                    <span class="input-group-text">원</span>
                  </div>
                </div>
              </div><br>
              <div class="form-group">
                <label for="reason">사유</label>
                <input type="text" class="form-control" id="reason" placeholder="내용을 입력해주세요" v-model="formData.reason" />
              </div><br>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" @click="closeModal">취소</button>
                <button type="submit" class="btn btn-primary">등록</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>

    <!-- 사이드바 추가 -->
    <ReceiptSidebar v-if="!isMobile" :item="selectedItem" :visible="sidebarVisible" @close="closeSidebar" />

    <!-- 이미지 미리보기 모달 -->
    <div v-if="isPreviewVisible" class="modal preview-modal">
      <div class="preview-modal-content">
        <img :src="previewImage" class="modal-image" />
        <span class="close" @click="closePreviewModal">&times;</span>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue';
import * as bootstrap from 'bootstrap';
import ReceiptSidebar from './ReceiptSidebar.vue'; // 사이드바 컴포넌트 임포트

const data = ref([
  {
    date: '2024-08-01',
    type: '식비',
    reason: '야근',
    amount: '100,000원',
    receiptName: '깃허브 사진',
    receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4',
    status: '신청중',
    rejectionReason: '',
    people: [
      { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
      { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
    ],
  },
  {
    date: '2024-08-02',
    type: '식비',
    reason: '야근',
    amount: '110,000원',
    receiptName: '깃허브 사진',
    receipt: 'https://avatars.githubusercontent.com/u/48048508?v=4',
    status: '신청중',
    rejectionReason: '',
    people: [
      { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
      { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '심청이', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '똑순이', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '복순이', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '황순이', position: '선임' },
    ],
  },
  // 추가 데이터...
]);

const selectedItem = ref(null);  // 사이드바에 표시할 선택된 항목
const sidebarVisible = ref(false);  // 사이드바 가시성 제어
const isMobile = ref(window.innerWidth <= 1150);
const isPreviewVisible = ref(false); // 이미지 미리보기 모달 가시성 제어
const previewImage = ref(''); // 미리보기할 이미지 URL
const currentPage = ref(1);
const itemsPerPage = ref(5);
const formData = ref({
  receiptImage: null,
  date: '',
  type: '식비',
  amount: '',
  reason: ''
});

// 페이지네이션 데이터
const totalPages = computed(() => Math.ceil(data.value.length / itemsPerPage.value));
const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * itemsPerPage.value;
  return data.value.slice(start, start + itemsPerPage.value);
});

const goToPage = (page) => {
  currentPage.value = page;
};

const openModal = () => {
  const modal = document.getElementById('receiptModal');
  const bootstrapModal = new bootstrap.Modal(modal);
  bootstrapModal.show();
};

const closeModal = () => {
  const modal = document.getElementById('receiptModal');
  const bootstrapModal = bootstrap.Modal.getInstance(modal);
  bootstrapModal.hide();
};

const handleFileChange = (event) => {
  formData.value.receiptImage = event.target.files[0];
};

const submitReceipt = () => {
  console.log('Receipt Submitted:', formData.value);
  closeModal();
};

const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1150;
};

const openSidebar = (item) => {
  if (!isMobile.value) {
    selectedItem.value = item;
    sidebarVisible.value = true;
  }
};

const closeSidebar = () => {
  sidebarVisible.value = false;
};

const openPreviewModal = (imageUrl) => {
  previewImage.value = imageUrl;
  isPreviewVisible.value = true;
};

const closePreviewModal = () => {
  isPreviewVisible.value = false;
};

// 모바일로 전환 시 사이드바 자동 닫기
watch(isMobile, (newVal) => {
  if (newVal) {
    closeSidebar();
  }
});

const getStatusClass = (status) => {
  if (status === '신청중') return 'status-pending';
  if (status === '승인') return 'status-approved';
  if (status === '반려') return 'status-rejected';
  return '';
};

onMounted(() => {
  window.addEventListener('resize', updateIsMobile);
});

const visiblePeopleLists = ref({});

const togglePeopleList = (item) => {
  if (visiblePeopleLists.value[item.date]) {
    delete visiblePeopleLists.value[item.date];
  } else {
    visiblePeopleLists.value[item.date] = true;
  }
};

const showPeopleList = (item) => {
  return visiblePeopleLists.value[item.date];
};
</script>

