  width: 100%;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 5px;
}

.card-header {
  font-size: 1.2rem;
  font-weight: bold;
}

.sidebar {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  transition: all 0.3s ease;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
}

.status-approved {
  color: green;
  font-weight: bold;
}
.status-pending {
  color: blue;
  font-weight: bold;
}
.status-rejected {
  color: red;
  font-weight: bold;
}

.search-button {
  font-size: 0.875rem;
  padding: 0.4rem 0.75rem;
  margin-bottom: 5px;
}

/* 카드 이미지 미리보기 모달 스타일 */
.preview-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 2000;
}

.preview-modal-content {
  position: relative;
  width: 80%;
  max-width: 500px;
  background-color: #fff;
  padding: 0;
  text-align: center;
}

.modal-image {
  width: 100%;
  height: auto;
}

.preview-modal .close {
  position: absolute;
  top: -50px;
  right: -30px;
  font-size: 40px;
  cursor: pointer;
  background: none;
  border: none;
  color: white;
}

/* 인원 목록 스타일 추가 */
.people-list {
  font-size: 0.7rem;
  max-height: 150px;
  overflow-y: auto;
  margin-top: 10px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}

.people-list-header {
  font-weight: bold;
  margin-bottom: 5px;
}

.people-list-item {
  padding: 5px 0;
}
</style>
